<template>
  <div id="app">
    <div
      v-if="notInstallTronLink"
      class="caution"
    >Please enable Tronlink or visit this page in a tron dapp support browser to interact with the Cannabis game</div>
    <b-loading is-full-page :active.sync="isLoading">
      <div style="text-align: center;">
        <img :src="publicPath+'loading.svg'" />
        <p>
          <small>{{loadingStatus}}</small>
        </p>
      </div>
    </b-loading>
    <Navbar />
    <section class="section" v-if="!isLoading">
      <div class="container">
        <transition
          name="custom-classes-transition"
          enter-active-class="animated fadeInDown"
          leave-active-class
        >
          <router-view />
        </transition>
      </div>
    </section>
  </div>
</template>
<script>
import Navbar from "@/components/Navbar";

export default {
  data() {
    return {
      notInstallTronLink: false,
      isLoading: true,
      publicPath: process.env.BASE_URL
    };
  },
  components: { Navbar },
  computed: {
    loadingStatus: function() {
      return this.$store.state.loadingStatus;
    }
  },
  async mounted() {
    let self = this;
    let checkTronLink = async function() {
      self.notInstallTronLink =
        window.tronWeb == undefined ||
        window.tronWeb.defaultAddress.base58 == false;
      if (!self.notInstallTronLink) {
        await self.$store.dispatch(
          "setMyAdress",
          window.tronWeb.defaultAddress.base58
        );
        await self.$store.dispatch(
          "setNetwork",
          window.tronWeb.solidityNode.host == "https://api.trongrid.io"
        );
        await self.$store.dispatch("loadDataFromContract");
        self.isLoading = false;
      }
    };
    setTimeout(checkTronLink, 2000);
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/app.scss";
.navbar-item img {
  max-height: 50px;
}
.caution {
  padding-top: 100px;
  text-align: center;
  background-color: #f00;
  height: 100%;
  width: 100%;
  z-index: 9999;
  position: fixed;
}
</style>
