<template>
  <nav class="navbar is-primary" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <router-link class="navbar-item" to="/">
        <img src="@/assets/brand.png" width="112" height="28" />
      </router-link>
      <a
        role="button"
        class="navbar-burger burger"
        :class="{'is-active':isActive}"
        aria-label="menu"
        aria-expanded="false"
        data-target="navbarBasicExample"
        @click="isActive =! isActive"
      >
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>
    <div id="navbarBasicExample" class="navbar-menu" :class="{'is-active':isActive}">
      <div class="navbar-start">
        <router-link class="navbar-item" to="/">Home</router-link>
        <router-link class="navbar-item" to="/growroom">GrowRoom</router-link>
        <router-link class="navbar-item" to="/account">My account</router-link>
      </div>
      <div class="navbar-end">
        <div class="navbar-item text-center">
          <b-button
            class="address"
            size="is-light"
            @click="goToAccount()"
            icon-left="account"
          >{{address}}</b-button>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { setTimeout } from "timers";
export default {
  data() {
    return {
      isActive: false,
      address: "... loading ..."
    };
  },
  mounted() {
    let self = this;
    let loadAddress = function() {
      self.address = self.$store.state.myAddress;
    };
    setTimeout(loadAddress, 3000);
  },
  methods: {
    goToAccount: function() {
      this.$router.push("account");
    }
  }
};
</script>

<style>
.address .icon {
  border: solid 1px;
  border-radius: 50%;
}
.button.address {
  border-radius: 30px;
}
.text-center {
  text-align: center;
}
</style>
