import axios from "axios"

async function getTokenInfo({ isTrc10, tokenIdOrAddress, mainnet }) {
    let returnInfo = {
      imgsrc: "https://via.placeholder.com/100",
      name: "NoName", 
      decimal: 0
    };
    mainnet = mainnet == undefined? true: mainnet;
    let netRootAPI = mainnet?'https://apilist.tronscan.org/api':'https://api.shasta.tronscan.org/api';
    let apiLink = netRootAPI + 
      (isTrc10
        ? "/token?id="
        : "/token_trc20?contract=") +
      tokenIdOrAddress;
    let response = await axios.get(apiLink);
    if (isTrc10) {
      returnInfo.imgsrc = response.data.data[0].imgUrl != undefined ? response.data.data[0].imgUrl : returnInfo.imgsrc;
      returnInfo.name = response.data.data[0].abbr != undefined? response.data.data[0].abbr : returnInfo.name;
      returnInfo.decimal = response.data.data[0].precision != undefined ? response.data.data[0].precision : 0;
    }
    if (
      !isTrc10 &&
      response.data.trc20_tokens != undefined &&
      response.data.trc20_tokens.length > 0
    ) {
      returnInfo.imgsrc = response.data.trc20_tokens[0].icon_url != undefined ? response.data.trc20_tokens[0].icon_url : returnInfo.imgsrc ;
      returnInfo.name = response.data.trc20_tokens[0].symbol != undefined ? response.data.trc20_tokens[0].symbol : returnInfo.name;
      returnInfo.decimal = response.data.trc20_tokens[0].decimals != undefined ? response.data.trc20_tokens[0].decimals : returnInfo.decimal;
    }
    return returnInfo;
  }

  export {getTokenInfo}