import { getTokenInfo } from "./../Utils/Tronscan.js";
import sleep from "./../Utils/Sleep";

var contract = {
  ctInstance: {},
  plans: [],
  elements: [],
  factories: [],
  speedUps: [],
  mainnet: true,
  loadContract: async function(contractAddress, isMainnet) {
    this.mainnet = isMainnet;
    this.ctInstance = await window.tronWeb.contract().at(contractAddress);
  },
  loadElements: async function() {
    this.elements = [];
    let elementCount = Number(await this.ctInstance.getElementsCount().call());
    for (let id = 0; id < elementCount; id++) {
      await this.loadElement(id);
      await this.loadElementInfoFromChain(id);
    }
  },
  loadElement: async function(id) {
    let elementFromBlockChain = await this.ctInstance.getElement(id).call();
    if (this.elements[id] == undefined) this.elements[id] = {};
    this.elements[id].id = id;
    this.elements[id].isTrc10 = elementFromBlockChain[0];
    this.elements[id].tokenIdOrAddress = elementFromBlockChain[0]
      ? elementFromBlockChain[1].toString()
      : window.tronWeb.address.fromHex(elementFromBlockChain[2]);
    this.elements[
      id
    ].availableHavestBalance = elementFromBlockChain[3].toNumber();
    this.elements[id].totalUserBalance = elementFromBlockChain[4].toNumber();
  },
  loadElementInfoFromChain: async function(id) {
    let { imgsrc, name, decimal } = await getTokenInfo({
      isTrc10: this.elements[id].isTrc10,
      tokenIdOrAddress: this.elements[id].tokenIdOrAddress,
      mainnet: this.mainnet
    });
    this.elements[id].imgsrc = imgsrc;
    this.elements[id].name = name;
    this.elements[id].decimal = decimal;
  },
  loadBalances: async function(address) {
    for (let id = 0; id < this.elements.length; id++) {
      await this.loadBalance(id, address);
    }
  },
  loadBalance: async function(id, address) {
    let balanceElementFromBlockChain = await this.ctInstance
      .balanceElements(address, id)
      .call();
    this.elements[id].myBalance = balanceElementFromBlockChain.toNumber();
  },
  loadFactories: async function() {
    this.factories = [];
    let factoriesCount = Number(
      await this.ctInstance.getStrainsCount().call()
    );
    for (let i = 0; i < factoriesCount; i++) {
      await this.loadFactory(i);
    }
  },
  loadFactory: async function(id) {
    let factoryTimeToGrown = await this.ctInstance
      .getStrainTimeToGrown(id)
      .call();
    let elementCountFromChain = await this.ctInstance
      .getStrainElementsCount(id)
      .call();
    let inElementsCount = elementCountFromChain.inElementsCount.toNumber();
    let outElementsCount = elementCountFromChain.outElementsCount.toNumber();
    let inElements = [];
    for (let x = 0; x < inElementsCount; x++) {
      let elementId = (await this.ctInstance
        .getStrainData(id, x, true, false)
        .call()).toNumber();
      let elementRatio = (await this.ctInstance
        .getStrainData(id, x, true, true)
        .call()).toNumber();
      inElements.push({ id: elementId, ratio: elementRatio });
    }
    let outElements = [];
    for (let x = 0; x < outElementsCount; x++) {
      let elementId = (await this.ctInstance
        .getStrainData(id, x, false, false)
        .call()).toNumber();
      let elementRatio = (await this.ctInstance
        .getStrainData(id, x, false, true)
        .call()).toNumber();
      outElements.push({ id: elementId, ratio: elementRatio });
    }

    this.factories[id] = {
      id: id,
      inElements: inElements,
      outElements: outElements,
      timeToGrown: factoryTimeToGrown.toNumber()
    };
  },
  loadPlans: async function(myAddress) {
    this.plans = [];
    let plansCount = Number(
      await this.ctInstance.getMyPlantsCount().call()
    );
    for (let i = 0; i < plansCount; i++) {
      await this.loadPlan(i, myAddress);
    }
  },
  loadNewPlans: async function(myAddress) {
    let plansCount = Number(
      await this.ctInstance.getMyPlantsCount().call()
    );
    if (plansCount > this.plans.length) {
      for (let i = this.plans.length; i < plansCount; i++) {
        await this.loadPlan(i, myAddress);
      }
      return true;
    }
    return false;
  },
  loadPlan: async function(id, myAddress) {
    let loop = true;
    do {
      let planFromBlockChain = await this.ctInstance
        .plants(myAddress, id)
        .call();
      let plan = {
        id: id,
        startTime: planFromBlockChain.startTime.toNumber(),
        bonusTime: planFromBlockChain.bonusTime.toNumber(),
        harvestTime: planFromBlockChain.harvestTime.toNumber(),
        mulSeed: planFromBlockChain.mulSeed.toNumber(),
        factoryId: planFromBlockChain.strainId.toNumber()
      };
     
      this.plans[id] = plan;
      if (plan.startTime == 0) {
        await sleep(3000);
      } else {
        loop = false;
      }
    } while (loop);
  },

  loadSpeedUps: async function() {
    let speedUpCount = Number(
      await this.ctInstance.getElementGrowthXPCount().call()
    );
    for (let i = 0; i < speedUpCount; i++) {
      await this.loadSpeedUp(i);
    }
  },
  loadSpeedUp: async function(id) {
    let speedUpFromBlockChain = await this.ctInstance
      .getElementGrowthXP(id)
      .call();
    let speedUp = {
      id: id,
      elementId: speedUpFromBlockChain.elementId.toNumber(),
      elementConsum: speedUpFromBlockChain.elementConsum.toNumber(),
      minutesBonus: speedUpFromBlockChain.minutesBonus.toNumber()
    };
    this.elements[speedUp.elementId].elementConsum = speedUp.elementConsum;
    this.elements[speedUp.elementId].minutesBonus = speedUp.minutesBonus;
    this.elements[speedUp.elementId].isSpeedUp = true;
    this.elements[speedUp.elementId].speedUpId = id;
  },
  speedUp: async function(planId, tokenId, mulBonus) {
    await this.ctInstance
      .growthXP(planId, this.elements[tokenId].speedUpId, mulBonus)
      .send();
  },
  plant: async function(factoryId, mulSeed) {
    await this.ctInstance.plant(factoryId, mulSeed).send();
  },
  harvest: async function(planId) {
    await this.ctInstance.harvest(planId).send();
  },
  withdrawElement: async function(elementId, amount) {
    await this.ctInstance.withdrawElement(elementId, amount).send();
  },
  depositElement: async function(element, amount) {
    if (element.isTrc10) {
      await this.ctInstance
        .depositElement(element.id, amount)
        .send({ tokenId: element.tokenIdOrAddress, tokenValue: amount });
    } else {
      let trc20 = await window.tronWeb.contract().at(element.tokenIdOrAddress);
      await trc20.approve(this.ctInstance.address, amount).send();
      await this.ctInstance.depositElement(element.id, amount).send();
    }
  }
};
export default contract;
